<template>
  <div>
    <el-form :inline="true" :model="addList" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="addList.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="addList.desc" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="menuList" border style="width: 100%">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="name" label="" width="180">
        <template slot-scope="scope">
          <el-input :value="scope.row.name" v-if="scope.row.showEdit" v-model="scope.row[scope.column.property]"></el-input>
          <span v-else>{{scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="desc" label="">
        <template slot-scope="scope">
          <el-input :value="scope.row.desc" v-if="scope.row.showEdit" v-model="scope.row[scope.column.property]"></el-input>
          <span v-else>{{scope.row.desc}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleFinish(scope.$index, scope.row)" v-if="scope.row.showEdit"></el-button>
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)" v-else></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          <el-button size="mini" @click="scope.row.showEdit=false" v-if="scope.row.showEdit"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
  name: "main-types",
  data() {
    return {
      addList: {
        name: "",
        desc: ""
      },
      menuList: [{ name: "", desc: "", showEdit: false }]
    };
  },
  methods: {
    ...mapActions([
      "handleAddTypesList",
      "handleDeleteType",
      "handleUpdataType"
    ]),
    handleEdit(index, row) {
      row.showEdit = true;
    },
    handleFinish(index, row) {
      if (row.name === "") {
        this.$message.error("");
        return;
      } else {
        const typedata = {};
        typedata.id = row.id;
        typedata.name = row.name;
        if (row.desc !== "") {
          typedata.desc = row.desc;
        }
        this.handleUpdataType(typedata).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.$message({
              type: "success",
              message: "!"
            });
            this.getTypesInfo();
          } else if (res.data.code === 0) {
            this.$message.error(res.data.errMsg);
          } else if (res.data.code === -1) {
            this.$message.error(res.data.errMsg);
          }
        });
        row.showEdit = false;
      }
    },
    handleDelete(index, row) {
      this.$confirm(` "${row.name}" `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.handleDeleteType(row.id).then(res => {
            if (res.data.code === 1) {
              this.$message({
                type: "success",
                message: "!"
              });
              this.getTypesInfo();
            } else {
              this.$message({
                message: "",
                type: "warning"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        })
        .then(() => {
          this.handleDeleteType(row.id).then(res => {
            if (res.data.code === 1) {
              this.$message({
                type: "success",
                message: "!"
              });
            } else {
              this.$message({
                message: "",
                type: "warning"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    addTypesList() {
      if (!this.addList.name) {
        this.$message.error("");
        return;
      }
      this.handleAddTypesList(this.addList)
        .then(res => {
          if (res.data.code === 1) {
            this.$message({
              message: "",
              type: "success"
            });

            // 
            this.getTypesInfo();
          } else if (res.data.code === 0) {
            this.$message.error(res.data.errMsg);
          } else if (res.data.code === -1) {
            this.$message.error(res.data.errMsg);
          }
        })
        .catch(err => {
          console.log(err.response);
        });
    },
    onSubmit() {
      this.addTypesList();
      this.addList = {
        name: "",
        desc: ""
      };
    },
    getTypesInfo() {
      this.$store
        .dispatch("handleGetTypeInfo")
        .then(res => {
          res.list.forEach(item => {
            item.showEdit = false;
          });
          this.menuList = res.list;
        })
        .catch(err => {
          console.log(err.response);
        });
    }
  },
  mounted() {
    this.getTypesInfo();
  }
};
</script>

<style>
</style>



