<template>
  <el-dialog
    :title="status ? '' : ''"
    :visible.sync="dialogVisible"
    width="40%"
    :before-close="handleClose">
    <el-form :inline="true" :model="dishesForm" class="demo-form-inline" size="small">
      <el-form-item label="">
        <el-input v-model="dishesForm.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="dishesForm.img" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="dishesForm.cid" placeholder="">
          <el-option
            v-for="item in typeList"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="dishesForm.price" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="dishesForm.desc" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <!-- <el-button type="primary" @click="add"></el-button> -->
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="editForm(false)"> </el-button>
      <el-button type="primary" @click="editForm(true)"> </el-button>
    </span>
  </el-dialog>
</template>
<script>
export default {
  name: 'dishes-edit',
  props: {
    dialogVisible: Boolean,
    typeList: Array,
    dishesForm: Object,
    status: Number
  },
  data () {
    return {
      show: false,
    }
  },
  methods: {
    editForm (e) {
      // this.show = false
      this.$emit('edit', e)
      console.log(this.dishesForm)
      console.log('parent', this.$parent.dishesForm)
    },
    handleClose () {
      this.$parent.dialogVisible = this.show
      this.$parent.clear()
    }
  }
}
</script>




